.wp-block-create-block-infobox {
	.info-box {
		border-color: var(--borderColor);
		border-style: var(--BorderType);
		margin: 0px 0px 0px 0px;
		background: var(--background);
		margin: var(--margin);
		padding: var(--padding);
		transition: 0.4s ease-in-out;


		&:hover {
			border-color: var(--borderHoverColor);
			background: var(--backgroundHover);
			box-shadow: var(--boxShadowHover);


		}

		.info-box__title {
			line-height: 1.2;
			margin: 0px 0px 0px 0px;
			text-align: var(--contentAlign);

			.text {
				font-size: 18px;
				font-weight: 700;
				margin-top: var(--titleMarginTop);
				margin-right: var(--titleMarginRight);
				margin-bottom: var(--titleMarginBottom);
				margin-left: var(--titleMarginLeft);
				padding: 0px;
				color: var(--titleColor);

				&:hover {
					color: var(--titleHoverColor);
				}
			}
		}

		.info-box__content {
			color: #050505;
			font-size: 16px;
			font-weight: 400;
			line-height: 1.5;
			margin: 0px 0px 0px 0px;
			text-align: var(--contentAlign);

			p {
				margin-top: var(--ContentMarginTop);
				margin-right: var(--ContentMarginRight);
				margin-bottom: var(--ContentMarginBottom);
				margin-left: var(--ContentMarginLeft);
				color: var(--ContentColor);

				&:hover {
					color: var(--ContentHoverColor);
				}
			}
		}

		.info-box__icon {
			font-size: 30px;
			color: #1a261f;
			padding: 0px 0px 0px 0px;
			margin: 0px 0px 0px 0px;
			background: transparent;
			text-align: center;
			display: block;

			.dashicons {
				font-size: 40px;
			}
		}

		.info-box__read-more {
			display: flex;
			align-items: center;
			justify-content: var(--readMoreAlign);

			.info-box__read-more-content {
				display: flex;
				align-items: center;
				gap: 4px;
				cursor: pointer;

				&.hasButton {
					background: var(--readMoreBackground);
					color: var(--readMoreColor);

					border-style: var(--callBorderType);
					border-color: var(--callBorderColor);
					border-radius: var(--callBorderRadius);
					border-color: var(--callBorderColor);

					&:hover {
						background: var(--readMoreHoverBack);
						color: var(--readMoreHoverColor);
						transition: all 0.3s ease-in-out;
						border-color: var(--callBorderHoverColor);
					}
				}

				a {
					display: inline-flex;
					align-items: center;
					justify-content: center;
					text-decoration: none;
					color: var(--readMoreColor);

					font-size: 16px;
					cursor: pointer;

					&:hover {
						color: var(--readMoreHoverColor);
					}

					p {
						margin: 0;
						font-size: 15px;


					}

					span {
						margin-left: 5px;

					}
				}


			}
		}
	}

	/* Default: All hidden */
	.desktopHide,
	.MobileHide,
	.tabHide {
		display: none;
	}


	@media (max-width: 767px) {

		.desktopHide,
		.tabHide {
			display: block;
		}

		.MobileHide {
			display: none;
		}

		.info-box {
			padding: var(--padding-mobile);
			margin: var(--margin-mobile);
			border-width: var(--BorderWidth-mobile);
			border-radius: var(--BorderRadius-mobile);
			box-shadow: var(--boxShadowControl-mobile) var(--boxShadow);

			&:hover {
				box-shadow: var(--boxShadowControl-mobile) var(--boxShadowHover)
			}

			.info-box__title {
				.text {
					margin: var(--TitleMargin-mobile);
				}
			}

			.info-box__content {
				p {
					margin: var(--ContentMargin-mobile);
				}
			}

			.info-box__read-more {
				.info-box__read-more-content {
					&.hasButton {
						padding: var(--callActionPadding-mobile);
						border-width: var(--callBorderWidth-mobile);
						border-radius: var(--callBorderRadius-mobile);
					}
				}
			}
		}


	}

	/* Tablet - 768px to 1024px */
	@media (min-width: 768px) and (max-width: 1024px) {

		.desktopHide,
		.MobileHide {
			display: block;
		}

		.tabHide {
			display: none;
		}

		.info-box {
			padding: var(--padding-tablet);
			margin: var(--margin-tablet);
			border-width: var(--BorderWidth-tablet);
			border-radius: var(--BorderRadius-tablet);
			box-shadow: var(--boxShadowControl-tablet) var(--boxShadow);

			&:hover {
				box-shadow: var(--boxShadowControl-tablet) var(--boxShadowHover)
			}

			.info-box__title {
				.text {
					margin: var(--TitleMargin-tablet);
				}
			}

			.info-box__content {
				p {
					margin: var(--ContentMargin-tablet);

				}
			}

			.info-box__read-more {
				.info-box__read-more-content {
					&.hasButton {
						padding: var(--callActionPadding-tablet);
						border-width: var(--callBorderWidth-tablet);
						border-radius: var(--callBorderRadius-tablet);
					}
				}
			}
		}


	}

	/* Desktop - 1025px and up */
	@media (min-width: 1025px) {

		.MobileHide,
		.tabHide {
			display: block;
		}

		.desktopHide {
			display: none;
		}

		.info-box {
			padding: var(--padding-desktop);
			margin: var(--margin-desktop);
			border-width: var(--BorderWidth-desktop);
			border-radius: var(--BorderRadius-desktop);
			box-shadow: var(--boxShadowControl-desktop) var(--boxShadow);

			&:hover {
				box-shadow: var(--boxShadowControl-desktop) var(--boxShadowHover)
			}

			.info-box__title {
				.text {
					margin: var(--TitleMargin-desktop);
				}
			}

			.info-box__content {
				p {
					margin: var(--ContentMargin-desktop);
				}
			}

			.info-box__read-more {
				.info-box__read-more-content {
					&.hasButton {
						padding: var(--callActionPadding-desktop);
						border-width: var(--callBorderWidth-desktop);
						border-radius: var(--callBorderRadius-desktop);
					}
				}
			}
		}
	}
}